"use client";
import React, { useEffect, useRef } from 'react';

type SeriesPoint = { x: number; y: number };

export default function RealTimeGraph({
  title = 'Series',
  color = '#2de8a7',
  data = [] as SeriesPoint[],
  height = 120
}: {
  title?: string;
  color?: string;
  data?: SeriesPoint[];
  height?: number;
}){
  const ref = useRef<SVGSVGElement | null>(null);

  // Simple SVG polyline renderer. Recalculates path when data changes.
  useEffect(()=>{
    const svg = ref.current;
    if(!svg) return;
    const w = svg.clientWidth || 300;
    const h = height;
    if(!data || data.length===0){
      const poly = svg.querySelector('polyline');
      if(poly) poly.setAttribute('points','');
      return;
    }

    const xs = data.map(p=>p.x);
    const ys = data.map(p=>p.y);
    const xmin = Math.min(...xs); const xmax = Math.max(...xs);
    const ymin = Math.min(...ys); const ymax = Math.max(...ys);
    const xRange = xmax - xmin || 1;
    const yRange = ymax - ymin || 1;

    const points = data.map(p=>{
      const x = ((p.x - xmin)/xRange) * w;
      const y = h - ((p.y - ymin)/yRange) * h;
      return `${x},${y}`;
    }).join(' ');

    const poly = svg.querySelector('polyline');
    if(poly) poly.setAttribute('points', points);
  }, [data, height]);

  return (
    <div style={{width:'100%'}}>
      <div style={{display:'flex', justifyContent:'space-between', marginBottom:6}}>
        <strong style={{fontSize:13}}>{title}</strong>
        <div style={{fontSize:12, color:'var(--muted)'}}>{data.length} pts</div>
      </div>
      <svg ref={ref} height={height} style={{width:'100%'}}>
        <defs>
          <linearGradient id="g1" x1="0" x2="0" y1="0" y2="1">
            <stop offset="0%" stopColor={color} stopOpacity="0.18" />
            <stop offset="100%" stopColor={color} stopOpacity="0.02" />
          </linearGradient>
        </defs>
        <rect x={0} y={0} width="100%" height={height} fill="url(#g1)" />
        <polyline fill="none" stroke={color} strokeWidth={2} points="" strokeLinecap="round" strokeLinejoin="round" />
      </svg>
    </div>
  );
}
